import React from 'react';

import CasesDisplay from './Display';

import State from './state';
import {AboutInfo} from './AboutInfo';


class Home extends React.Component
{

    render()
    {
        const {india} = this.props.data;
        const {tested} = this.props.data;
        const {statewise} = this.props.data;
        return (
            <div className="container" >
                <div className="row mt-3 pl-3">
                     <div>
                         <p style={{color:'#7e09ea'}}>{this.props.data.default}</p>
                         <p style={{fontSize:'10px'}}>Last updated 3 mins ago</p>
                     </div>
                </div>
                <div className="row">
                    <div className="col" >
                                <div className="row" >
                                <div className="casesDisplayer" >
                                        <CasesDisplay key={india} data={india}/>
                                </div>
                                </div>
                                <div className="row">
                                <div className="total states mt-3">
                                <table className="table table-bordered">
                                <thead>
                                <tr style={{overflow:'hidden',fontSize:'10px'}}>
                                <th scope="col" style={{width:'40px'}}>State</th>
                                <th scope="col" style={{color:'red'}}>Confirmed</th>
                                <th scope="col" style={{color:'blue'}}>Active</th>
                                <th scope="col" style={{color:'green'}}>Recovered</th>
                                <th scope="col" style={{color:'black  '}}>Deaths</th>
                                </tr>
                                </thead>
                                <tbody>
                                {statewise.map(s=> <State name="state"  key={s.statecode}  data={s} />)}

                                </tbody>
                            </table>
                            </div>
                                </div>
                                
                    </div>
                    <div className="col-md-4">
                            <AboutInfo data={tested} name="india" />
                    </div>
                  
                     
            </div>
             </div>
        );
    }

}
export default Home;