import React from 'react';

import CasesDisplay from './Display';

import State from './state';

import {AboutInfo} from './AboutInfo';

class StateInfo extends React.Component
{
    state ={
        currentState :[],
        districts :[],
        tested:[]
        
    }
    componentDidMount(){
        this.setState({tested:this.props.getStateTestData(this.props.match.params.code)});
        const {statewise} = this.props;
        const current=  statewise.filter(s => s.statecode === this.props.match.params.code)[0];
        const currentState = current=== undefined ? {} : current;
        this.setState({currentState});
        
        const {districtwise} = this.props;

        const d = districtwise.filter(d=>d.statecode === this.props.match.params.code);
         let district = [];   
        if(d.length !==0)
            district = d[0]['districtData'];

       // console.log(district);
        this.setState({districts:district});
    }
    render()
    {
      
        
        
        return(
            <div className="container" >
                <div className="row mt-3 pl-3">
                     <div>
                         <p style={{color:'#7e09ea'}}>{this.state.currentState.state}</p>
                         <p style={{fontSize:'10px'}}>Last updated 3 mins ago</p>
                     </div>
                </div>
                <div className="row">
                    <div className="col" >
                                <div className="row" >
                                <div className="casesDisplayer" >
                                 <CasesDisplay key={this.currentState} data={this.state.currentState}/>
                                </div>
                                </div>
                                <div className="row">
                                <div className="total states mt-3">
                                <table className="table table-bordered">
                                <thead>
                                <tr style={{overflow:'hidden',fontSize:'10px'}}>
                                <th scope="col" style={{width:'40px'}}>Districts</th>
                                <th scope="col" style={{color:'red'}}>Confirmed</th>
                                <th scope="col" style={{color:'blue'}}>Active</th>
                                <th scope="col" style={{color:'green'}}>Recovered</th>
                                <th scope="col" style={{color:'black  '}}>Deaths</th>
                                </tr>
                                </thead>
                                <tbody>
                {this.state.districts.map(d=><State name="district" key={d.district} style={this.style} data={d} />)}
                                </tbody>
                            </table>
                            </div>
                                </div>
                                
                    </div>
                    <div className="col-md-4">
                            <AboutInfo data={this.state.tested} name="state"/>
                    </div>
                    
                  
                     
            </div>
             </div>
                           
          

        )
    }
}

export default StateInfo;